@font-face{font-family:'Yekan-Bakh';src:url(/soally/static/media/Yekan-Bakh.64cadbed.TTF) format("truetype");font-weight:normal;font-style:normal}*{text-align:center;font-family:"Yekan-Bakh";font-weight:bold}body{direction:rtl;margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.Start-Col *{color:#e3e3e3}.Start-Col img{width:25%}.Start-Col h1{font-size:46px}.Start-Col Button{padding:15px 70px;position:relative;border:5px solid transparent;border-radius:35px;background:#ed865d;background:linear-gradient(-90deg, #ed865d 0%, #f197bd 100%);background-clip:padding-box}.Start-Col Button::after{position:absolute;top:-5px;bottom:-5px;left:-5px;right:-5px;background:linear-gradient(90deg, #f197b8 0%, rgba(237,136,104,0.47) 100%);content:"";z-index:-1;border-radius:35px}.Start-Col Button:focus{box-shadow:none;outline:none}@media only screen and (max-width: 768px){.Start-Col img{width:40%}.Start-Col h1{font-size:30px}}

.logo img{width:230px}@media only screen and (max-width: 768px){.logo img{width:191px}}

#main{height:100vh;min-height:100vh;position:relative}#row{min-height:75%}#footer{min-height:25%}

#footer-tag *{color:#e3e3e3}#footer-tag #footer-app-name-intro{font-size:31px}#footer-tag #footer-app-name{font-size:34px;padding:0 10px;background:#b83b5e;background:linear-gradient(-90deg, #b83b5e 0%, #ef895d 100%);border-radius:20px 10px 10px  10px;font-family:'Yellowtail', cursive;font-weight:normal}#footer-tag #footer-des{font-size:15px}@media only screen and (max-width: 768px){#footer-tag #footer-app-name-intro{font-size:25px}#footer-tag #footer-app-name{font-size:25px}#footer-tag #footer-des{font-size:15px}}

#questions-row{min-height:65%}#questions-row h1{font-size:46px;font-weight:bold;color:#e3e3e3}#stepper{direction:ltr}#Qustions-footer{min-height:35%}.choice{background-color:#df5e88 !important;font-size:25px !important;padding:5px 20px !important;border:none !important;border-radius:44px !important;margin:4.5px 0px !important}.choice:active{border:none !important;outline:none !important}@media only screen and (min-width: 767px){.choice{margin:0px 0px !important}}

